body {
  -webkit-user-select: none;
  user-select: none;
  background-image: url("background-light-sand.afb8b4d1.jpg");
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.toolbar {
  text-align: center;
  width: 100%;
}

#drawApp {
  touch-action: none;
  background-image: url("background-turquoise.37b10085.jpg");
  position: absolute;
}

#canvas {
  touch-action: none;
}

#drawApp:not(:fullscreen) {
  display: none;
  position: absolute;
}

#drawButton {
  position: absolute;
  bottom: 10px;
  left: 10px;
}

#appBtn {
  background-image: url("background-turquoise.37b10085.jpg");
  border: 4px solid gray;
  border-radius: 20px;
  padding: 10px;
}

#canvasStack {
  border: 2.5px solid #07162c;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  position: relative;
}

.layer {
  opacity: .8;
  position: absolute;
  top: 0;
  left: 0;
}

#appContainer {
  grid-template-columns: 5em auto 5em;
  width: 100%;
  height: 100%;
  display: grid;
}

#canvasDiv {
  padding: 10px;
}

#toolbar, #toolbar2 {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1em;
  margin: 1em;
  display: flex;
}

#layersToolbar {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.hidden {
  display: none !important;
}

.loadOptionsContainer {
  align-items: center;
  display: flex;
}

.loadOptionsList {
  gap: 1em;
  margin: 0 1em;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.loadOptionsItem {
  opacity: .9;
  background-color: #d3d3d3;
  border-radius: 10px;
}

.layerButtons {
  border-radius: 10px;
  width: 3em;
  height: 3em;
  margin: 5px;
  padding: .1em;
}

.layerButtons.layerButtonActive {
  border: 5px solid #daa520;
  border-radius: 10px;
  padding: 0;
}

.button:active {
  filter: brightness(70%);
}

.printerButton, .trashButton {
  margin-top: auto;
}

#toolbar img, #toolbar2 img {
  opacity: .8;
  width: 3em;
}

